.appdownload {
  div {
    width: 100%;
    position: relative;
    img {
      display: block;
      width: 100%;
    }
    .appdownloadbtn1 {
      width: 80%;
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      -webkit-transform: translateX(-50%);
      -moz-transform: translateX(-50%);
      -o-transform: translateX(-50%);
    }
    .appdownloadbtn2 {
      width: 80%;
      position: absolute;
      bottom: 1.5rem;
      left: 50%;
      transform: translateX(-50%);
      -webkit-transform: translateX(-50%);
      -moz-transform: translateX(-50%);
      -o-transform: translateX(-50%);
      border-top: 1px solid #2ca8ef;
      padding-top: 1rem;
      img {
        width: 100%;
        display: block;
      }
    }
  }
}
.share-ios, .share-android {
  position: fixed;
  display: none;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 99;
  left: 0;
  top: 0;
}
.share-ios img, .share-android img {
  width: 100%;
}
